import { withPrefix } from 'gatsby';
import { linkAnalytics } from '../../helpers/analytics';

export interface VideoProps {
  name: string;
}

/**
 *
 */
const Video = ({ name }: VideoProps) => {
  const path = withPrefix(`/videos/${name}`);
  return (
    <video controls className="c-blog-video" poster={`${path}.png`}>
      <source src={`${path}.webm`} type="video/webm" />
      <source src={`${path}.mp4`} type="video/mp4" />
      <track kind="captions" src={`${path}.vtt`} srcLang="en" />
      <p>
        Your browser does not support HTML video. Here is a
        <a onClick={linkAnalytics} href={`${path}.mp4`}>
          link to the video
        </a>{' '}
        instead.
      </p>
    </video>
  );
};

export default Video;
